<template>
  <div class="pageContent">
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="权益名称：">
            <el-input v-model="searchList.equityName"></el-input>
          </el-form-item>
          <el-form-item label="权益状态：">
            <el-select v-model="searchList.status" clearable>
              <el-option label="未使用" value="1"></el-option>
              <el-option label="使用中" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <el-button @click="newShow" type="text" icon="el-icon-circle-plus-outline">新增权益</el-button>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="权益名称" prop="equityName"></el-table-column>
        <el-table-column label="权益图标" width="100">
          <template slot-scope="scope">
            <el-image style="width: 50px; height: 50px" :src="scope.row.equityUrl" fit="fill"></el-image>
          </template>
        </el-table-column>
        <el-table-column label="权益说明">
          <template slot-scope="scope">
            {{scope.row.comment ? scope.row.comment : '-'}}
          </template>
        </el-table-column>
        <el-table-column label="权益状态" width="100" prop="statusName"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button 
              v-if="scope.row.isDefault == 2" 
              @click="editItem(scope.row)"
              type="text" 
              size="small"
            >编辑</el-button>
            <el-button 
              v-if="scope.row.isDefault == 2" 
              :disabled="scope.row.status == 1" 
              type="text" 
              size="small" 
              @click="deleteItem(scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <!-- 新增权益弹框 -->
    <el-dialog title="新增权益" :visible.sync="newFormShow" width="50%" :append-to-body="true" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form :model="newForm" label-width="120px">
        <el-form-item label="权益名称：" required>
          <el-input style="width:200px;" v-model="newForm.equityName" maxlength="6" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="权益图标：" required>
          <el-button :disabled="newForm.showImg" type="primary" @click="showImgMaterialDia('add')">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
          <span class="dangerFont" style="font-size:14px;">建议尺寸72*72px</span>
        </el-form-item>
        <el-form-item v-if="newForm.showImg" label="" required>
          <el-popover placement="right" trigger="hover">
            <el-button @click.stop="deleteImg('add')" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
            <el-image slot="reference" style="width: 72px; height: 72px" :src="newForm.equityUrl" fit="fill"></el-image>
          </el-popover>
        </el-form-item>
        <el-form-item label="权益说明：">
          <el-input style="width:200px;" type="textarea" placeholder="最多输入50个字" v-model="newForm.comment" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newFormShow = false">取 消</el-button>
        <el-button type="primary" :loading="newLoading" @click="newFormSubmit">保 存</el-button>
      </span>
    </el-dialog>
    <!-- 编辑权益弹框 -->
    <el-dialog title="编辑权益" :visible.sync="editFormShow" width="50%" :append-to-body="true" :show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form :model="editForm" label-width="120px">
        <el-form-item label="权益名称：" required>
          <el-input style="width:200px;" v-model="editForm.equityName" maxlength="6" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="权益图标：" required>
          <el-button :disabled="editForm.showImg" type="primary" @click="showImgMaterialDia('edit')">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
          <span class="dangerFont" style="font-size:14px;">建议尺寸72*72px</span>
        </el-form-item>
        <el-form-item v-if="editForm.showImg" label="" required>
          <el-popover placement="right" trigger="hover">
            <el-button @click.stop="deleteImg('edit')" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
            <el-image slot="reference" style="width: 72px; height: 72px" :src="editForm.equityUrl" fit="fill"></el-image>
          </el-popover>
        </el-form-item>
        <el-form-item label="权益说明：">
          <el-input style="width:200px;" type="textarea" placeholder="最多输入50个字" v-model="editForm.comment" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editFormShow = false">取 消</el-button>
        <el-button type="primary" :loading="editLoading" @click="editFormSubmit">保 存</el-button>
      </span>
    </el-dialog>
    <!-- 图片库 -->
    <el-dialog title="图片库" :visible.sync="showImgMaterial" width="70%">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="onlyclose"
        @subMit="subMit"
      ></imageMaterial>
    </el-dialog>
  </div>
</template>
<script>
import imageMaterial from '@/components/shopMaterial/imageMaterial';
import { mapState } from 'vuex'
import { 
  getVipEquityList ,
  getEquity ,
  saveEquity ,
  deleteEquity ,
  updateEquity
} from '@/api/memberManage/memberRoom'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      list:[],
      listTotal:0,
      newFormShow:false,
      newForm:{
        showImg:false
      },
      newLoading:false,
      editFormShow:false,
      editForm:{
        showImg:false
      },
      editLoading:false,
      // 图片库
      showImgMaterial:false,
      getPost:false,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.equityType = 1
    this.getData()
  },
  methods:{
    getData(){
      getVipEquityList(this.searchList).then(res=>{
        console.log('权益列表数据',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.equityName = ''
      this.searchList.status = ''
      this.getData()
    },
    deleteItem(e){
      this.$confirm('确定删除此权益？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteEquity(e.id).then(res=>{
          console.log('删除权益',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 新增权益弹出框
    newShow(){
      // 默认写死
      this.newForm.equityType = 1;
      // 
      this.newForm.shopId = this.shop.shopId;
      this.newForm.createId = this.user.userId;
      this.newFormShow = true;
    },
    // 新增权益
    newFormSubmit(){
      console.log('请求新增的数据',this.newForm)
      if(!this.newForm.equityName){
        this.$message.error('请正确输入权益名称');
        return;
      }
      if(!this.newForm.equityUrl){
        this.$message.error('请上传权益图标后再试');
        return;
      }
      this.newLoading = true;
      saveEquity(this.newForm).then(res=>{
        console.log('新增权益',res)
        if(res.code == 200){
          // 还原状态
          this.newForm.showImg = false;
          this.newForm.equityName = '';
          this.newForm.equityUrl = '';
          this.newForm.comment = '';
          // 
          this.newFormShow = false;
          this.$message({
            message: '新增权益成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.newLoading = false;
          },1000)
          this.getData()
        }else{
          this.newLoading = false;
          this.$message.error(res.message);
        }
        
      })
    },
    editItem(e){
      console.log('编辑',e)
      getEquity(e.id).then(res=>{
        console.log('编辑的权益详情',res)
        if(res.code == 200){
          this.editForm = res.data;
          this.editForm.showImg = true;
          this.editFormShow = true;
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 编辑权益提交
    editFormSubmit(){
      console.log('编辑权益的数据',this.editForm)
      if(!this.editForm.equityName){
        this.$message.error('请正确输入权益名称');
        return;
      }
      if(!this.editForm.equityUrl){
        this.$message.error('请上传权益图标后再试');
        return;
      }
      this.editLoading = true;
      updateEquity(this.editForm).then(res=>{
        console.log('编辑权益',res)
        if(res.code == 200){
          // 
          this.editFormShow = false;
          this.$message({
            message: '编辑权益成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.editLoading = false;
          },1000)
          this.getData()
        }else{
          this.editLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
    /**
    * 图片库操作方法
    */
    showImgMaterialDia(e){
      this.getPost = true;
      this.showImgMaterial = true;
      this.showImgMaterialType = e
    },
    onlyclose(e){
      console.log('值点了关闭',e)
      this.showImgMaterial = false;
      this.getPost = false;
    },
    subMit(e){
      console.log('点击了确定',e)
      this.showImgMaterial = false;
      this.getPost = false;
      if(this.showImgMaterialType == 'add'){
        this.newForm.showImg = true;
        this.newForm.equityUrl = e[0].imageUrl
      }
      if(this.showImgMaterialType == 'edit'){
        this.editForm.showImg = true;
        this.editForm.equityUrl = e[0].imageUrl
      }
      
    },
     // 二级分类 - 删除图片
    deleteImg(e){
      console.log('执行了删除',e)
      if(e == 'add'){
        this.newForm.showImg = false;
        this.newForm.equityUrl = ''
        console.log(this.newForm)
      }
      if(e == 'edit'){
        console.log('进来了')
        this.editForm.showImg = false;
        this.editForm.equityUrl = ''
        console.log(this.editForm)
      }
      
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
   components: {
    imageMaterial
  }
}
</script>